'use strict';

document.getElementById("btnAR").addEventListener('click', rtlFuction);

function rtlFuction (e){
    document.getElementsByTagName("body")[0].classList.toggle("rtl");
    document.getElementById("ar").classList.toggle("dis-none");

    document.getElementById("en").classList.toggle("dis-none");
    document.getElementById("enFooter").classList.toggle("dis-none");
    document.getElementById("arFooter").classList.toggle("dis-none");
    document.getElementById("h3Ar").classList.toggle("dis-none");
    document.getElementById("h3En").classList.toggle("dis-none");
    document.getElementById("h2ProAr").classList.toggle("dis-none");
    document.getElementById("h2ProEn").classList.toggle("dis-none");
    document.getElementById("pro1Ar").classList.toggle("dis-none");
    document.getElementById("pro1En").classList.toggle("dis-none");
    document.getElementById("pro2Ar").classList.toggle("dis-none");
    document.getElementById("pro2En").classList.toggle("dis-none");
    document.getElementById("pro3Ar").classList.toggle("dis-none");
    document.getElementById("pro3En").classList.toggle("dis-none");
    document.getElementById("enNote").classList.toggle("dis-none");
    document.getElementById("arNote").classList.toggle("dis-none");
    var x = document.getElementsByClassName("next");
    var y = document.getElementsByClassName("prev"); 

   if (e.target.value == "English") {
       e.target.value = "عربى"
       for (i = 0; i < x.length; i++) {
        x[i].style.transform= "rotateY(-180deg)";
        y[i].style.transform= "rotateY(180deg) ";

      }
   }else{
         e.target.value = "English"
         for (i = 0; i < x.length; i++) {
            x[i].style.transform= "rotateY(180deg) ";
            y[i].style.transform= "rotateY(-180deg) ";
          }
   }


   var i;

   console.log(x);
}